import React, { Component } from 'react';
import glamorous from 'glamorous';
import PrettyFormRow from './components/PrettyFormRow';
import PrettyGutter from './components/PrettyGutter';
import PrettyHeader from './components/PrettyHeader';
import PrettyInput from './components/PrettyInput';
import TrackerButtons from './components/TrackerButtons';
import moment from 'moment';
import tokens from './tokens';
import './fonts.css';
import './reset.css';

const AppContainer = glamorous.div({
  width: `${tokens.space*80}px`,
  display: 'block',
  padding: `${tokens.space*2}px`,
});

class App extends Component {
  state = {
    currentTime: '',
  }
  componentDidMount() {
    this.setTick();
  }

  setTick = () => {
    setInterval(() => {
      console.log('1')
      this.setState({ currentTime: moment().clone().format('MMM/DD/YYYY hh:mm:ss') });
    }, 1000);
  }

  onTimeSelected = (value) => {
    debugger
  }

  render() {
    return (
      <AppContainer>
        <PrettyGutter>
          <PrettyHeader>{ this.state.currentTime }</PrettyHeader>
        </PrettyGutter>
        <PrettyGutter>
          <PrettyFormRow label="task">
            <PrettyInput />
          </PrettyFormRow>
        </PrettyGutter>
        <PrettyGutter>
          <PrettyFormRow label="description">
            <PrettyInput />
          </PrettyFormRow>
        </PrettyGutter>
        <PrettyGutter>
          <PrettyFormRow label="Time">
            <TrackerButtons onTimeSelected={this.onTimeSelected} />
          </PrettyFormRow>
        </PrettyGutter>
      </AppContainer>
    );
  }
}

export default App;



